@import '../../_variables.scss';
.films {
  margin-top: 70px;
  padding: 20px;
  background: #000;
  &__wrapper {
    display: flex;
    gap: 40px;
    flex-wrap: wrap;
    margin-bottom: 50px;
    @media (max-width: 769px) {
      gap: 20px;
      justify-content: space-around;
    }
  }
  &__link {
    display: block;
    width: 200px;
    height: 300px;
  }
  &__pagination {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__loadMore {
    padding: 12px 22px;
    border-radius: 18px;
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    -ms-border-radius: 18px;
    -o-border-radius: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: $mainСolor;
    background-color: rgb(230, 93, 14);
    font-size: 16px;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    &:hover {
      background-color: rgb(192, 78, 11);
      color: #fff;
    }
  }
}
